/* Basic styles for the elements */
.markdown h1 {
  font-size: 2em;
  font-weight: bold;
  margin-bottom: 0.5em;
}

.markdown h2 {
  font-size: 1.5em;
  font-weight: bold;
  margin-bottom: 0.5em;
}

.markdown h3 {
  font-size: 1.17em;
  font-weight: bold;
  margin-bottom: 0.5em;
}

.markdown p {
  margin-bottom: 1em;
}

.markdown ul,
.markdown ol{
  margin-bottom: 0.6em;
  margin-left: 1em;
}
.markdown li {
  display: list-item;
}
.markdown ol {
  list-style:decimal;
}
.markdown ul {
  list-style:initial;
}

.markdown a {
  text-decoration: underline;
}

.markdown a:hover {
  text-decoration: none;
}

.markdown strong {
  font-weight: bold;
}

.markdown em {
  font-style: italic;
}

.markdown code {
  font-family: Consolas, monospace;
  background-color: theme(colors.neutral2);
}

.markdown blockquote {
  margin: 1em 0;
  padding: 0.5em 1em;
  border-left: 2px solid theme(colors.contrast2);
}

.markdown pre {
  font-family: Consolas, monospace;
  background-color: theme(colors.neutral2);
  border: 1px solid theme(colors.contrast2);
  overflow: auto;
  padding: 0.5em;
}

.markdown hr {
  border: 1px solid theme(colors.contrast2);
  margin: 0.5em 0;
}
